html {
  height: 100%;
  background-color: #303030;
}

.title {
  color: #fff;
  font-size: 3em;
  margin-bottom: 0.05em;
}

.subtitle {
  color: rgb(255, 92, 92);
  margin-bottom: 2em;
}

.centerform {
  margin: auto;
  vertical-align: middle;
  margin-top: 10vh;
}

.submitbtn {
  width: 100%;
}

footer {
  padding: 50px;
  color: #fff;
}

.App {
  height: 100%;
  text-align: center;
}

.App-logo {
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
